@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');

* {
  font-family: 'VT323', monospace;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #FDB813;
  background-image: url(img/bg1.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.App {
  text-align: center;
}

h1 {
  color: #1A1A40;
  margin-bottom: 0.5rem;
  font-size: 3rem;
  text-shadow: 2px 2px #fff;
}

.TodoWrapper {
  background: #E86363;
  margin-top: 5rem;
  padding: 3rem;
  border-radius: 50px;
  box-shadow: 2px 2px #1A1A40;
}

.TodoForm {
  width: 100%;
}

.todo-input {
  outline: none;
  background: none;
  border: 2px solid #1A1A40;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: 600px;
  color: #1A1A40;
  font-size: 1.2rem;
  border-radius: 15px;
}

.todo-input::placeholder {
  color: #1a1a40;
  font-size: 1.2rem;
}

.todo-btn {
  background: #1A1A40;
  color: #fff;
  border: none;
  padding: 0.75rem;
  margin-left: 10px;
  cursor: pointer;
  font-size: 1.2rem;
  border-radius: 15px;
  transition: background-color 0.2s ease-in-out;
}

.todo-btn:hover {
  background-color: #fff;
  color: #1A1A40;
}

.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #1A1A40;
  color: #fff;
  padding: 0.75rem 1rem;
  margin-top: 10px;
  border-radius: 25px;
  margin-bottom: 1rem;
  cursor: pointer;
  box-shadow: 2px 2px #FDB813;
}

.fa-trash {
  margin-left: 0.75rem;
  font-size: 1.2rem;
}

.completed {
  color: #1A1A4054;
  text-decoration: line-through;
}
@media only screen and (max-width: 768px) {
  body {
  background-image: none;
  }
  
  .TodoWrapper {
  padding: 2rem;
  margin-top: 1rem;
  border-radius: 25px;
  }
  
  .todo-input {
  width: 100%;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
  }
  
  .Todo > p {
  margin: 0.5rem 0;
  font-size: 16px;
  }
  
  .Todo > div {
  display: flex;
  align-items: center;
  }
  
  .todo-btn {
  margin-left: 0;
  margin-top: 0.5rem;
  width: 100%;
  }

  .fa-trash {
  margin-left: 0;
  font-size: 1rem;
  padding-left: 1rem;
  }

  .TodoWrapper {
  padding: 1rem;
  }

}